import React from 'react';
import {Row, Col, Card, Input} from 'antd';
import socket from "./socketio";


const devices = [
    {
        key: "light",
        name: "Đèn",
        currentStatus: "off",
        status: {
            on: {
                icon: require("./icon/light_on.png"),
                text: "Bật",
            },
            off: {
                icon: require("./icon/light_off.png"),
                text: "Tắt",
            },

        }
    },
    {
        key: "tivi",
        name: "Tivi",
        currentStatus: "off",
        status: {
            on: {
                icon: require("./icon/tivi_on.png"),
                text: "Bật",
            },
            off: {
                icon: require("./icon/tivi_off.png"),
                text: "Tắt",
            },

        }
    },
    {
        key: "fan",
        name: "Quạt",
        currentStatus: "off",
        status: {
            on: {
                icon: require("./icon/fan_on.png"),
                text: "Bật",
            },
            off: {
                icon: require("./icon/fan_off.png"),
                text: "Tắt",
            },

        }
    },
    {
        key: "airconditioner",
        name: "Điều hóa",
        currentStatus: "off",
        status: {
            on: {
                icon: require("./icon/air_conditioner_on.png"),
                text: "Bật",
            },
            off: {
                icon: require("./icon/air_conditioner_off.png"),
                text: "Tắt",
            },

        }
    },
    {
        key: "door",
        name: "Cửa",
        currentStatus: "close",
        status: {
            close: {
                icon: require("./icon/door_close.png"),
                text: "Đóng",
            },
            open: {
                icon: require("./icon/door_open.png"),
                text: "Mở",
            },
            lock: {
                icon: require("./icon/door_lock.png"),
                text: "Khóa",
            }

        }
    },
    {
        key: "gate",
        name: "Cổng",
        currentStatus: "close",
        status: {
            close: {
                icon: require("./icon/gate_close.png"),
                text: "Đóng",
            },
            open: {
                icon: require("./icon/gate_open.png"),
                text: "Mở",
            },
            lock: {
                icon: require("./icon/gate_lock.png"),
                text: "Khóa",
            }

        }
    },
]

class Device extends React.Component {
    state = {
        devices: devices
    };

    componentWillMount() {
        socket.on("data", (data) => {
            let arr = data.split("_");
            let devices = this.state.devices.map((device) => {
                if (device.key == arr[0]) {
                    device.currentStatus = arr[1];
                }

                return {...device}
            })
            this.setState({devices});
        })
    }

    render() {
        return (
            <div>
                <Input placeholder="Basic usage" onPressEnter={(e) => {
                    let data = e.target.value;

                    let arr = data.split("_");
                    let devices = this.state.devices.map((device) => {
                        if (device.key == arr[0]) {
                            device.currentStatus = arr[1];
                        }

                        return {...device}
                    })
                    this.setState({devices});
                    console.log({devices});
                    console.log({arr});
                }}/>
                    <Row gutter={24}>
                    {
                        this.state.devices.map((device) => {
                            return (
                                <Col span={8}>
                                    <Card title={device.name} style={{marginBottom: 24}}>
                                        <img src={device.status[device.currentStatus].icon}/>
                                        <div style={{
                                            marginTop: 10,
                                            fontWeight: "bold",
                                            textTransform: "uppercase"
                                        }}>{device.status[device.currentStatus].text}</div>

                                    </Card>
                                </Col>
                            )
                        })
                    }
                    </Row>
                    </div>

                    );
                }
                }

                export default Device;
